import * as React from "react";
import styled from "styled-components";
import * as github from "./images/github.png";

const FooterComponent = styled.footer`
    margin: 20px auto;
    width: 70%;
    padding: 20px;

    text-align: center;
    border-top: 1px solid;

    ul {
        list-style: none;
        li img {
            opacity: 0.5;
        }

        li img:hover {
            opacity: 1;
        }
    }

    .last-updated {
        font-size: 10pt;
    }

    @media (max-width: 768px) {
        width: auto;
        ul li img {
            opacity: 1;
        }
    }
`;

export default class Footer extends React.PureComponent {
    public render() {
        return (
            <FooterComponent>
                <ul>
                    <li>
                        <a href="https://github.com/agupta1989" target="_blank">
                            <img src={github} alt="github"/>
                        </a>
                    </li>
                </ul>
                <p className="last-updated">Last Updated On July 8, 2019</p>
            </FooterComponent>
        );
    }
}



