import * as React from "react";
import styled from "styled-components";
import * as MyPhoto from "./images/my_photo.jpg";

const HeaderComponent = styled.header`
    padding: 60px;
    text-align: center;
    background: #1abc9c;
    color: white;
    font-size: 30px;

    .profile_picture {
        border-radius: 50%;
    }
`;
interface HeaderInterface {
    text: string;
}

export default class Header extends React.Component<HeaderInterface, {}> {
    public render() {
        return (
            <HeaderComponent>
                <img className="profile_picture" src={MyPhoto} alt="" />
                <h2>{this.props.text}</h2>
            </HeaderComponent>
        );
    }
}


